	      var newPos = (evalResultset.filteredrows.length === 0) ? -1 : 0;
	
	      // wasn't in old, shouldn't be now... do nothing
	      if (oldPos === -1 && newPos === -1) return;
	
	      // wasn't in resultset, should be now... add
